<template>
  <div class="toy-filter" v-if="filterBy">
    <h2>Filter By:</h2>
    <section class=" options flex">
     <label for="toyName">Toy Name:</label>
        <input id="toyName" type="text" v-model="filterBy.name" placeholder="Enter Toy Name.." @input="filter" />
      <label for="toyType">Type:</label>
        <select id="toyType" v-model="filterBy.type" @change="filter">
          <option value="all">All</option>
          <option value="adult">Adult</option>
          <option value="funny">Funny</option>
          <option value="educational">Educational</option>
        </select>
      <label for="inStock">In Stock?</label>
        <select id="inStock" v-model="filterBy.inStock" @change="filter">
          <option value="all">All</option>
          <option value="true">Yes</option>
          <option value="false">No</option>
        </select>
      <label for="sort">Sort by:</label>
        <select id="sort" v-model="filterBy.sortBy" @change="filter">
          <option value="name">Name</option>
          <option value="price">Price</option>
        </select>
    </section>
  </div>
</template>

<script>
export default {
  name: "toy-filter",
  data() {
    return {
      filterBy: {
        name: "",
        inStock: "all",
        type: "all",
        sortBy: "name",
      },
    };
  },

  computed: {
    filter() {
      return this.debounce(this.setFilter, 500);
    },
  },
  methods: {
    debounce(filterFunction, delay) {
      let timer;
      return function() {
        clearTimeout(timer);
        timer = setTimeout(() => {
          filterFunction();
        }, delay);
      };
    },
    setFilter() {
      this.$emit("filter", this.filterBy);
    },
  },
};
</script>


