<template>
<div>
  <GmapMap
     ref="mapRef"
    :center="{ lat: 32.584139, lng: 35.005980 }"
    :zoom="10"
    map-type-id="terrain"
    style="width: 500px; height: 300px"
  >
    <GmapMarker
      :key="index"
      v-for="(m, index) in markers"
      :position="m.pos"
      :clickable="true"
      :draggable="true"
      @click="center = m.position"
    />
  </GmapMap>
  <button @click="panToStore(markers[0].pos)">Store 1</button>
  <button @click="panToStore(markers[1].pos)">Store 2</button>
  <button @click="panToStore(markers[2].pos)">Store 3</button>
  </div>
</template>

<script>
export default {
  name: "Google-Maps",
  data() {
    return {
      markers: [
        { pos: { lat: 32.462448, lng: 35.043359 } },
        { pos: { lat: 32.570758, lng: 34.954274 } },
        { pos: { lat: 32.647831, lng: 35.095029 } },
      ],
    };
  },
  methods:{
    panToStore(pos){
        this.$refs.mapRef.$mapPromise
        .then((map) => {
          map.panTo(pos)
        })
    }
  }
};
</script>

<style></style>
