import Vue from "vue";
import VueRouter from "vue-router";
import toyApp from '../views/toy-app.vue';
import toyDetails from '../views/toy-details.vue'
import toyEdit from '../views/toy-edit.vue'
// import toyExtras from '../views/toy-extras.vue'
import toyAbout from '../views/toy-about.vue'


Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "App",
    component: toyApp,
  },
  {
    path: "/toy/:id",
    name: "toyDetails",
    component: toyDetails,
  },
  {
    path: "/edit/:id?",
    name: "toyEdit",
    component: toyEdit,
  },
  {
    path: "/about",
    name: "aboutPage",
    component: toyAbout,
  },
];

const router = new VueRouter({ routes });

export default router;
